import "core-js/modules/es.function.name.js";
import { ElMessage, ElMessageBox } from 'element-plus';
import axios from 'axios';
export default {
  name: 'upload',
  data: function data() {
    return {
      loading: false,
      headers: {
        'Content-Type': 'multipart/form-data'
      },
      filename: ''
    };
  },
  methods: {
    // 上传文件
    upload: function upload() {
      ElMessageBox.confirm('确定提交', 'Warning', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(function () {
        var path = 'http://127.0.0.1:5000/addhost';
        axios.get(path).then(function (res) {
          // await addlink(form.value)
          ElMessage({
            message: '提交成功',
            type: 'success'
          });
        });
      }).catch(function () {
        ElMessage({
          type: 'info',
          message: '取消提交'
        });
      });
    }
  },
  // 文件大小限制
  beforeUpload: function beforeUpload(file) {
    var isLt2M = true;
    isLt2M = file.size / 1024 / 1024 < 100;

    if (!isLt2M) {
      this.loading = false;
      this.$message.error('上传文件大小不能超过 100MB!');
    }

    this.filename = file.name;
    return isLt2M;
  },
  // 上传成功回调
  handleSuccess: function handleSuccess(response, file, fileList) {
    this.$refs.upload.clearFiles();
    this.$notify({
      title: '上传成功',
      type: 'success',
      duration: 2500
    }); // ElMessage({
    //       message: '提交成功',
    //       type: 'success'
    //     })
  },
  // 上传失败
  handleError: function handleError(e, file, fileList) {
    var msg = JSON.parse(e.message);
    this.$notify({
      title: msg.message,
      type: 'error',
      duration: 2500
    });
    this.loading = false;
  }
};